import { CHANGE_MENU } from "./type"

const initialState = {
    selected_menu: 1,
    menus: [
        {key: 1, link: "/", component:"Home", name : ""},
        {key: 2, link: "/userlist", component: "UserList", name : " "},
        {key: 3, name : "", submenu:[{key:4, link: "/aptinfo", name: " "},{key:5, link: "/officeinfo", name: " "}]}
    ]
}

const menusReducer = (state=initialState, action) => {
    console.log('payload:',action.payload);
    switch(action.type) {
        case CHANGE_MENU:
            return {
                ...state,
                selected_menu: action.payload
            }
        default: return state;
    }
}

export default menusReducer;